// src/core/reportGenerator.js
export function generateReport({ contrastData, wcagData, udlData, aiSummary }) {
    const timestamp = new Date().toLocaleString();
    const title = document.title || 'Untitled Page';
    const url = window.location.href;

    const reportHtml = `
    <!DOCTYPE html>
    <html lang="ko">
    <head>
      <meta charset="UTF-8" />
      <title>${title} - Ïõπ Ï†ëÍ∑ºÏÑ± Î≥¥Í≥†ÏÑú</title>
      <style>
        body { font-family: Arial, sans-serif; padding: 20px; line-height: 1.6; }
        h1 { color: #1a73e8; }
        h2 { margin-top: 30px; color: #333; border-bottom: 1px solid #eee; }
        .section { margin-bottom: 20px; }
        .item { padding: 8px; border-left: 4px solid #ccc; margin: 8px 0; }
        .fail { border-color: #e53935; background: #fdecea; }
        .pass { border-color: #43a047; background: #e8f5e9; }
      </style>
    </head>
    <body>
      <h1>${title} - Ïõπ Ï†ëÍ∑ºÏÑ± Î∂ÑÏÑù Î≥¥Í≥†ÏÑú</h1>
      <p><strong>URL:</strong> ${url}</p>
      <p><strong>ÏÉùÏÑ±Ïùº:</strong> ${timestamp}</p>

      <div class="section">
        <h2>ÏÉâÏÉÅ ÎåÄÎπÑ Î∂ÑÏÑù</h2>
        ${contrastData.map(item => `
          <div class="item ${item.status}">
            <p><strong>${item.text}</strong></p>
            <p>ÎåÄÎπÑÏú®: ${item.contrast}</p>
          </div>
        `).join('')}
      </div>

      <div class="section">
        <h2>WCAG Î∂ÑÏÑù</h2>
        ${wcagData.map(item => `
          <div class="item ${item.status}">
            <p><strong>${item.id}: ${item.title}</strong></p>
            <p>${item.message}</p>
          </div>
        `).join('')}
      </div>

      <div class="section">
        <h2>UDL Î∂ÑÏÑù</h2>
        <ul>
          ${udlData.map(item => `<li>${item}</li>`).join('')}
        </ul>
      </div>

      ${aiSummary ? `
        <div class="section">
          <h2>AI Î∂ÑÏÑù ÏöîÏïΩ</h2>
          <div>${aiSummary}</div>
        </div>` : ''}

    </body>
    </html>
  `;

    const blob = new Blob([reportHtml], { type: 'text/html' });
    const urlObj = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = urlObj;
    a.download = `${title.replace(/\s+/g, '-')}-Ï†ëÍ∑ºÏÑ±-Î≥¥Í≥†ÏÑú.html`;
    a.click();
    URL.revokeObjectURL(urlObj);
}
export const downloadReport = generateReport;