// src/core/initPanel.js

import { checkColorContrast } from './checkColorContrast.js';
import { analyzeWCAG } from './axeAnalysis.js';
import { analyzeForUDL } from './udlAnalysis.js';
import { checkColorBlindness } from './colorBlindness.js';
import { initChat } from '../controller/chatController.js';
import { downloadReport } from './reportGenerator.js';
import { removeHighlights } from '../utils/contrastUtil.js';
import '../styles/panel.css';

export function initPanel() {
    if (document.getElementById('udl-accessibility-panel')) {
        document.getElementById('udl-accessibility-panel').remove();
        removeHighlights();
        return;
    }

    let userApiKey = sessionStorage.getItem('udl_api_key');
    if (!userApiKey) {
        userApiKey = prompt('UDL Î∂ÑÏÑùÏùÑ ÏúÑÌïú API KEYÎ•º ÏûÖÎ†•Ìï¥Ï£ºÏÑ∏Ïöî.');
        if (!userApiKey) {
            alert('API KEYÍ∞Ä ÏûÖÎ†•ÎêòÏßÄ ÏïäÏïòÏäµÎãàÎã§.');
            return;
        }
        sessionStorage.setItem('udl_api_key', userApiKey);
    }

    const panel = document.createElement('div');
    panel.id = 'udl-accessibility-panel';
    panel.innerHTML = `
    <div class="panel-header">
      <h2 class="panel-title">Ï†ëÍ∑ºÏÑ± Í≤ÄÏÇ¨</h2>
      <div class="panel-controls">
        <button class="panel-btn" id="minimize-panel" title="ÏµúÏÜåÌôî">‚Äì</button>
        <button class="panel-btn" id="close-panel" title="Îã´Í∏∞">√ó</button>
      </div>
    </div>
    <div class="panel-tabs">
      <button id="tab-contrast" class="tab-active">Î™ÖÎèÑ ÎåÄÎπÑ</button>
      <button id="tab-wcag">Ïõπ Ï†ëÍ∑ºÏÑ± Ìï≠Î™©</button>
      <button id="tab-udl">AI Ï†úÏïà</button>
      <button id="tab-colorblind">ÏÉâÎßπ ÏãúÎÆ¨Î†àÏù¥ÏÖò</button>
      <button id="tab-chat">AI Ï±óÎ¥á</button>
    </div>
    <div id="tab-content-contrast" class="tab-content">Í≤ÄÏÇ¨ Ï§ë...</div>
    <div id="tab-content-wcag" class="tab-content" style="display:none;">Í≤ÄÏÇ¨ Ï§ë...</div>
    <div id="tab-content-udl" class="tab-content" style="display:none;">Î∂ÑÏÑù Ï§ë...</div>
    <div id="tab-content-colorblind" class="tab-content" style="display:none;">ÏÉâÎßπ ÏãúÎÆ¨Î†àÏù¥ÏÖò Î°úÎî© Ï§ë...</div>
    <div id="tab-content-chat" class="tab-content" style="display:none;">Ï±óÎ¥á Î°úÎî© Ï§ë...</div>
    <div class="panel-footer">
      <button id="download-results" class="button secondary-btn">Î≥¥Í≥†ÏÑú Îã§Ïö¥Î°úÎìú</button>
      <button id="run-full-scan" class="button primary-btn">Ï†ÑÏ≤¥ Í≤ÄÏÇ¨ Ïã§Ìñâ</button>
    </div>
  `;
    document.body.appendChild(panel);

    // ÌÉ≠ Ï†ÑÌôò Ïù¥Î≤§Ìä∏
    const tabs = ['contrast', 'wcag', 'udl', 'colorblind', 'chat'];
    tabs.forEach(tab => {
        document.getElementById(`tab-${tab}`).onclick = () => activateTab(tab);
    });

    document.getElementById('download-results').onclick = () => downloadReport();
    document.getElementById('run-full-scan').onclick = () => {
        checkColorContrast();
        analyzeWCAG();
        analyzeForUDL();
        checkColorBlindness();
    };

    document.getElementById('close-panel').onclick = () => {
        panel.remove();
        removeHighlights();
    };

    function activateTab(tabId) {
        tabs.forEach(tab => {
            document.getElementById(`tab-content-${tab}`).style.display = 'none';
            document.getElementById(`tab-${tab}`).classList.remove('tab-active');
        });
        document.getElementById(`tab-content-${tabId}`).style.display = 'block';
        document.getElementById(`tab-${tabId}`).classList.add('tab-active');

        switch(tabId) {
            case 'contrast': checkColorContrast(); break;
            case 'wcag': analyzeWCAG(); break;
            case 'udl': analyzeForUDL(); break;
            case 'colorblind': checkColorBlindness(); break;
            case 'chat': initChat(); break;
        }
    }

    activateTab('contrast');
}
