___CSS_LOADER_EXPORT___.push([module.id, `/* panel.css */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

#udl-accessibility-panel {
    position: fixed;
    top: 20px;
    right: 20px;
    width: 600px;
    max-width: 90vw;
    height: 90vh;
    background: white;
    border-radius: 8px;
    box-shadow: 0 10px 30px rgba(0, 0, 0, 0.15);
    display: flex;
    flex-direction: column;
    z-index: 99999;
    overflow: hidden;
    resize: both;
    min-width: 300px;
    min-height: 300px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 14px;
    line-height: 1.5;
    color: #212529;
}

.panel-header {
    padding: 16px 20px;
    background: linear-gradient(135deg, #3a7bd5, #2c5282);
    color: white;
    font-weight: bold;
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: move;
}

.panel-title {
    font-size: 16px;
    display: flex;
    align-items: center;
    gap: 8px;
}

.panel-controls {
    display: flex;
    gap: 8px;
}

.panel-btn {
    background: none;
    border: none;
    color: white;
    font-size: 18px;
    cursor: pointer;
    width: 24px;
    height: 24px;
    border-radius: 4px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: background 0.2s;
}

.panel-btn:hover {
    background: rgba(255, 255, 255, 0.2);
}

.panel-tabs {
    display: flex;
    background-color: #f8f9fa;
    border-bottom: 1px solid #e9ecef;
    padding: 0 4px;
}

.panel-tabs button {
    flex: 1;
    padding: 12px 8px;
    border: none;
    background: none;
    cursor: pointer;
    font-weight: 500;
    color: #495057;
    position: relative;
    transition: all 0.2s;
}

.panel-tabs button:hover {
    color: #3a7bd5;
    background-color: rgba(58, 123, 213, 0.05);
}

.panel-tabs button.tab-active {
    color: #3a7bd5;
    font-weight: 600;
}

.panel-tabs button.tab-active::after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 10%;
    width: 80%;
    height: 3px;
    background-color: #3a7bd5;
    border-radius: 3px 3px 0 0;
}

.tab-content {
    flex: 1;
    overflow-y: auto;
    padding: 16px;
    background-color: #ffffff;
}

.panel-footer {
    display: flex;
    justify-content: space-between;
    padding: 12px 16px;
    border-top: 1px solid #e9ecef;
    background-color: #f8f9fa;
    gap: 8px;
}

.button {
    display: inline-flex;
    align-items: center;
    gap: 6px;
    padding: 8px 12px;
    border-radius: 4px;
    border: none;
    font-weight: 500;
    font-size: 13px;
    cursor: pointer;
    transition: background-color 0.2s;
}

.primary-btn {
    background-color: #3a7bd5;
    color: white;
}

.primary-btn:hover {
    background-color: #2c5282;
}

.secondary-btn {
    background-color: #e9ecef;
    color: #495057;
}

.secondary-btn:hover {
    background-color: #ced4da;
}

