// utils/color.js

/**
 * HEX ÏÉâÏÉÅÏùÑ RGB Í∞ùÏ≤¥Î°ú Î≥ÄÌôò
 * @param {string} hex - #RRGGBB ÌòïÏãù
 * @returns {{r: number, g: number, b: number}}
 */
export function hexToRgb(hex) {
    hex = hex.replace(/^#/, '');
    const bigint = parseInt(hex, 16);
    return {
        r: (bigint >> 16) & 255,
        g: (bigint >> 8) & 255,
        b: bigint & 255
    };
}

/**
 * RGB Í∞íÏùÑ HEX ÏΩîÎìúÎ°ú Î≥ÄÌôò
 * @param {number} r
 * @param {number} g
 * @param {number} b
 * @returns {string} - #RRGGBB ÌòïÏãù
 */
export function rgbToHex(r, g, b) {
    return (
        '#' +
        [r, g, b]
            .map(v => Math.max(0, Math.min(255, v)).toString(16).padStart(2, '0'))
            .join('')
    );
}

/**
 * Ï£ºÏñ¥ÏßÑ ÏÉâÏÉÅÏóê ÎåÄÎπÑÎêòÎäî ÌÖçÏä§Ìä∏ ÏÉâÏÉÅ Í≤∞Ï†ï (Ìù∞ÏÉâ ÎòêÎäî Í≤ÄÏùÄÏÉâ)
 * @param {string} hexColor - #RRGGBB
 * @returns {string} - 'black' or 'white'
 */
export function getContrastTextColor(hexColor) {
    const { r, g, b } = hexToRgb(hexColor);
    const brightness = (r * 299 + g * 587 + b * 114) / 1000;
    return brightness > 125 ? 'black' : 'white';
}

/**
 * ÏÉâÎßπ ÏãúÎÆ¨Î†àÏù¥ÏÖòÏö© ÏÉâÏÉÅ Îß§Ìä∏Î¶≠Ïä§ Ï†ÅÏö©
 * @param {string} hex - ÏõêÎ≥∏ ÏÉâÏÉÅ
 * @param {Array<number>} matrix - 4x5 ÌòïÏãùÏùò ÏÉâÎßπ Î≥ÄÌôò Îß§Ìä∏Î¶≠Ïä§
 * @returns {string} - Î≥ÄÌôòÎêú HEX ÏÉâÏÉÅ
 */
export function applyColorMatrix(hex, matrix) {
    const { r, g, b } = hexToRgb(hex);

    const newR = r * matrix[0] + g * matrix[1] + b * matrix[2];
    const newG = r * matrix[5] + g * matrix[6] + b * matrix[7];
    const newB = r * matrix[10] + g * matrix[11] + b * matrix[12];

    return rgbToHex(Math.round(newR), Math.round(newG), Math.round(newB));
}
