export function scrollToBottom(container = document.querySelector('.chat-messages')) {
    if (!container) return;

    try {
        container.scrollTop = container.scrollHeight;
        requestAnimationFrame(() => {
            container.scrollTop = container.scrollHeight;
        });
    } catch (err) {
        console.error('üìâ Ïä§ÌÅ¨Î°§ Ï≤òÎ¶¨ Ïò§Î•ò:', err);
    }
}